@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
:root {
  --color-primary: #6780fa;
  --color-primary-darker: #4d6aff;

  --transition-speed: 600ms;

  --color-navbar-text: #ccc5b9;
  --color-navbar-secondary: #403d39;
  --color-navbar-background: #252422;

  --color-background: #000;
  --color-note: #353535;
  --color-text: var(--color-navbar-text);
}

body {
  background-color: #000;
  background-color: var(--color-background);
  font-family: "Noto Sans", sans-serif;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background: #403d39;
  background: var(--color-navbar-secondary);
}

body::-webkit-scrollbar-thumb {
  background: #6780fa;
  background: var(--color-primary);
}

main {
  margin-left: 5rem;
}

@media only screen and (max-width: 600px) {
  main {
    margin-left: -4px;
    margin-bottom: 5.5rem;
  }
}

.login-form {
  display: flex;
  justify-content: center;

  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
}

.login-form h1 {
  display: flex;
  justify-content: center;
}

.login-form input {
  display: flex;
  font-size: 20px;
  margin-bottom: 20px;
  border: 0px solid #000;
  border-radius: 2px;
  text-align: center;
}

.login-form input[type="text"],
input[type="password"] {
  background-color: #000;
  color: white;
  /*text-decoration: underline white;*/
  text-underline-offset: 2.5px;
  width: 100vh;
}

input::-ms-clear,
input::-ms-reveal {
  display: none;
}

.login-form input[type="text"]:focus,
input[type="password"]:focus {
  outline: none;
}

.login-form input[type="submit"] {
  margin: 0 auto;
  font-weight: bold;
  padding: 5px 15px 5px 15px;
  color: white;
  background-color: var(--color-primary);
}

.login-form input[type="submit"]:hover {
  background-color: var(--color-primary-darker);
}

.login-form input[type="submit"]:focus {
  background-color: var(--color-primary-darker);
  outline: none;
}

.login-form img {
  width: 90px;
  position: absolute;
  top: -90%;
}

.wave-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.wave-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.wave-top svg,
.wave-bottom svg {
  display: block;
  width: calc(100% + 1.3px);
  height: 150px;
}

.wave-bottom,
.wave-top {
  fill: var(--color-primary-darker);
}

.navbar {
  position: fixed;
  background-color: var(--color-navbar-background);
  transition: width var(--transition-speed) ease;
  margin-left: -10px;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.nav-item {
  width: 100%;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: var(--color-navbar-text);
  text-decoration: none;
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
  transition: var(--transition-speed);
}

.logo .nav-link {
  -webkit-filter: grayscale(0%) opacity(0.7);
          filter: grayscale(0%) opacity(0.7);
}

.navbar:hover .logo .nav-link {
  transition: 2s;
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: var(--color-navbar-secondary);
  color: var(--color-navbar-text);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: var(--color-primary);
}

.fa-primary,
.fa-secondary {
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--color-navbar-text);
  background: #141414;
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: var(--transition-speed);
}

.logo-text {
  display: inline;
  position: absolute;
  left: -999px;
  transition: var(--transition-speed);
}

.navbar:hover .logo svg {
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
}

/* Small screens */
@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .logo {
    display: none;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
  }
}

/* Large screens */
@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 14rem;
  }

  .navbar:hover .link-text {
    display: inline;
  }

  .navbar:hover .logo svg {
    margin-left: 11rem;
  }

  .navbar:hover .logo-text {
    left: 5px;
  }
}

.notes-grid {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
}

.note {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-note);
  font-size: 2rem;
  color: var(--color-text);
  border-radius: 4px;
  transition: all 500ms;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.note:hover {
  background-color: var(--color-primary);
  color: white;
}

.note:hover + .star {
  color: white;
}

.star:hover {
  color: white;
}

.star {
  width: 40px;
  margin-top: -44px;
  margin-left: 5px;

  font-size: 2rem;
  color: var(--color-primary);
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.notes-create-button {
  display: flex;
  margin: 0 auto;
  background: var(--color-note);
  font-size: 5rem;
  color: var(--color-text);
  border: none;
  border-radius: 4px;

  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-bottom: 10px;
}

.notes-create-button:hover {
  background-color: var(--color-primary);
  color: white;
}

.note-detail {
  display: flex;
  justify-content: center;

  margin-top: 4%;
}

.note-detail input,
textarea {
  display: flex;
  font-size: 20px;
  margin-bottom: 20px;
  border: none;
  border-radius: 2px;
  background-color: var(--color-note);
  color: var(--color-navbar-text);
}

.note-detail input[type="text"],
textarea {
  border: 2px solid var(--color-note);
  width: 300px;
}

.note-detail input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: var(--color-navbar-text);
}

.note-detail input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: var(--color-navbar-text);
}

.note-detail input::placeholder,
textarea::placeholder {
  color: var(--color-navbar-text);
}

.note-detail textarea {
  height: 250px;
}

.note-detail input[type="text"]:focus,
textarea:focus {
  outline: 2px solid var(--color-navbar-text);
  outline-offset: 2px;
}

.note-detail input[type="submit"] {
  margin: 0 auto;
  margin-top: 30px;
  font-weight: bold;
  padding: 5px 15px 5px 15px;
  color: white;
  background-color: var(--color-primary);
}

.note-detail input[type="submit"]:hover {
  background-color: var(--color-primary-darker);
}

.note-detail label {
  margin-bottom: 4px;
  display: none; /*displayed none, in NoteCreation displays on change*/
  color: var(--color-navbar-text);
}

.files th,
td {
  color: var(--color-navbar-text);

  font-size: 1.5rem;
}

.files table {
}

.files th {
  background-color: var(--color-navbar-secondary);
}

.files td {
  background-color: var(--color-navbar-background);
}

.files table {
  width: 100%;
}

.file-td-center {
  text-align: center;
}

.file-td-download {
  color: var(--color-primary);
  cursor: pointer;
}

.truco {
  height: 688px;
  width: auto;
  background-color: black;
}

.truco h1 {
  color: white;
}

.truco .head2 {
  display: flex;
}

.truco .head h3 {
  margin-top: 90px;
}

.truco .head {
  display: flex;
}

.truco button,
canvas {
  position: absolute;
}

.truco button {
  margin-top: 100%;
  width: 50px;
  height: 50px;
  font-size: 40px;
  font-weight: bolder;
  color: #eb5e28;
  border: 2px #403d39 solid;
  background: #252422;
  border-radius: 5px;
}

